  padding: 0;
  margin: 0;
}

body {
  font-family: 'Poppins', sans-serif;
  background-color: white;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

/*
  AppBar
*/
header {
  display: flex;
  flex-direction: column;
}

.app-bar {
  padding: 13px 16px;
  background-color: white;
  display: grid;
  grid-template-columns: auto 1fr auto;
  gap: 10px;
  position: sticky;
  top: 0;
  z-index: 99;
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.2);
}

.app-bar .app-bar__menu {
  display: flex;
  align-items: center;
}

.app-bar .app-bar__menu button {
  background-color: transparent;
  border: none;
  font-size: 30px;
  font-weight: bold;
  padding: 4px 10px;
  color: #26577C;
  border: 1px solid #26577C;
  cursor: pointer;
  border-radius: 3px;
}

.app-bar .app-bar__brand {
  display: flex;
  align-items: center;
  justify-content: center;
}

.app-bar .app-bar__brand h1 {
  color: #26577C;
  text-transform: uppercase;
  font-size: 25px;
  user-select: none;
}

.app-bar .app-bar__navigation {
  position: absolute;
  top: 76px;
  left: -70%;
  width: 65%;
  min-height: 100vh;
  background-color: white;
  transition: all 0.3s;
  padding: 8px;
  overflow: hidden;
}

.app-bar .app-bar__navigation.open {
  left: 0;
}

.app-bar .app-bar__navigation ul {
  width: 100%;
  overflow: hidden;
}

.app-bar .app-bar__navigation ul li a {
  display: inline-block;
  text-decoration: none;
  color: black;
  padding: 10px;
  font-size: 18px;
  min-width: 44px;
  line-height: 44px;
  text-align: center;
  margin-bottom: 20px;
}

.app-bar .app-bar__navigation ul li a:hover {
  color: #E55604;
}

.skip-link {
  position: absolute;
  top: -42px;
  left: 0;
  text-decoration: none;
  outline: none;
  padding: 8px;
  box-sizing: border-box;
  min-width: 44px;
  min-height: 44px;
  background-color: #E55604;
  color: #F5F1FF;
}

.skip-link:focus {
  top: 0;
  z-index: 100;
}

/*
 * Hero Element
 */

.hero {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.hero h2 {
  position: absolute;
  width: 100%;
  color: #F5F1FF;
  text-align: center;
}

.hero img {
  width: 100%;
  min-height: 250px;
  object-fit: cover;
  object-position: center;
}

/*
  Main Content
*/

main {
  padding: 32px;
  flex: 1;
}

.content {
  margin: 0 auto;
  min-height: 100%;
}

.content .content__heading {
  font-weight: normal;
}

/*
  Restaurants
*/

.restaurants {
  display: grid;
  grid-template-columns: 1fr;
  gap: 16px;
  margin: 32px 0;
}

/*
  Restaurant Item
*/

.restaurant-item {
  width: 100%;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  border-radius: 8px;
  overflow: hidden;
  text-decoration: none;
}

.restaurant-item:hover{
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.603);
}

.restaurant-item__header {
  position: relative;
}

.restaurant-item .restaurant-item__header .restaurant-item__header__poster {
  width: 100%;
  height: 15rem;
}

.restaurant-item .restaurant-item__header .restaurant-item__header__rating {
  position: absolute;
  padding: 8px;
  bottom: -12px;
  left: 0;
  display: inline-block;
  background-color: black;
  color: white;
}

.restaurant-item .restaurant-item__header .restaurant-item__header__rating .restaurant-item__header__rating__score {
  margin-left: 10px;
}

.restaurant-item .restaurant-item__content {
  padding: 16px;
}

.restaurant-item .restaurant-item__content h3 {
  text-align: center;
  margin: 0 0 10px 0;
  color: #26577C;
  text-decoration: none;
}

.restaurant-item .restaurant-item__content p {
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 3; /* number of lines to show */
  -webkit-box-orient: vertical;
  color: rgba(0, 0, 0, 0.712);
}

/*
  Detail
*/

/*
restaurant
*/

.restaurant {
  margin: 0 auto;
  width: 100%;
  max-width: 800px;
  display: grid;
  grid-template-columns: 1fr;
  gap: 18px 16px;
}

.restaurant__title {
  color: #26577C;
  text-align: center;
}

.restaurant__info .address {
  margin-bottom: 20px;
}

.restaurant__info .menu {
  list-style: none;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  gap: 7px;
  margin-bottom: 20px;
}

.restaurant__info .menu li {
  border-radius: 20px;
  text-align: center;
  color: #26577C;
  background-color: #b4b4b328;
  padding: 3px;
}

.restaurant .restaurant__poster {
  width: 100%;
  max-width: 450px;
}

.restaurant .restaurant__info h4 {
  margin: 8px 0;
}

.restaurant .restaurant__info .title-reviews {
  margin-top: 30px;
}

.restaurant__info .container-reviews {
  display: grid;
  grid-template-columns: 1fr;
  gap: 16px;
}

.restaurant__info .reviews {
  color: #000000c5;
  background-color: #ffffff;
  padding: 10px;
  border-radius: 5px;
  box-shadow: 0 1px 4px 0 rgba(0, 0, 0, 0.2);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding-top: 20px;
  overflow: hidden;
}

.restaurant__info .reviews .comment {
  margin-bottom: 10px;
  font-size: 16px;
  color: #26577C;
}

.restaurant__info .reviews .name {
  font-weight: bold;
}

.restaurant__info .reviews .date {
  font-size: small;
}

/*
  Like button
*/

.like {
  background-color: #E55604;
  padding: 22px;
  border: none;
  border-radius: 20%;
  position: fixed;
  bottom: 20px;
  right: 10px;
  cursor: pointer;
}

.like .fa {
  font-size: 22px;
  color: #ffffff;
}
/*
  Footer
*/

footer {
  padding: 16px;
}

footer p {
  margin-top: 16px;

  text-align: center;
  color: #aaaaaa;
}

footer p span {
  color: #26577C;
  text-decoration: none;
}

/*
  Loading
*/

.lds-ring {
  display: inline-block;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%);
  width: 40px;
  height: 40px;
  padding: 10px;
  z-index: 99;
  background-color: rgba(0, 0, 0, 0.651);
}
.lds-ring div {
  box-sizing: border-box;
  display: block;
  position: absolute;
  width: 40px;
  height: 40px;
  border: 6px solid #ffffff;
  border-radius: 50%;
  animation: lds-ring 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
  border-color: #ffffff transparent transparent transparent;
}
.lds-ring div:nth-child(1) {
  animation-delay: -0.45s;
}
.lds-ring div:nth-child(2) {
  animation-delay: -0.3s;
}
.lds-ring div:nth-child(3) {
  animation-delay: -0.15s;
}
@keyframes lds-ring {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

/*
  Form tambah review
*/

.form-review {
  display: grid;
  grid-template-columns: 1fr;
  margin-top: 20px;
}

.form-group {
  display: flex;
  flex-direction: column;
}

.form-group label {
  margin-bottom: 4px;
  font-weight: lighter;
}

input[type=text], textarea {
  background: #F5F1FF;
  border: 2px solid #26577C;
  border-radius: 5px;
  padding: 10px;
  display: inline-block;
  min-width: 44px;
  min-height: 44px;
  font-size: 16px;
  box-sizing: border-box;
  margin-bottom: 8px;
}

input[type=text]:focus {
  background-color: #ffffff;
}

textarea:focus {
  background-color: #ffffff;
}

.btn-submit {
  width: fit-content;
  border-radius: 5px;
  padding: 10px;
  display: inline-block;
  min-width: 44px;
  min-height: 44px;
  background-color: #26577C;
  color: white;
  margin-top: auto;
  cursor: pointer;
  border: none;
}

.btn-submit:hover {
  background: #17354d;
  color: white;
}

input[type=text], textarea, .btn-submit:focus {
  outline: none;
